import * as React from 'react';
import {createMemoryRouter, RouterProvider, Outlet, useLocation, useNavigate} from "react-router-dom";
import {createRouter} from "@remix-run/router";
import {createRoot} from "react-dom/client";
import {PropsWithChildren, ReactElement, ReactNode, useEffect, useState} from "react";
import {RgbColor, RgbColorPicker} from "react-colorful";
import Homepage from "./pages/homepage";
import SpiralPage from "./pages/spiralpage";
import FirstTimeSafetyPage from "./pages/firsttimesafetypage";
import CustomizePage from "./pages/customizepage";
import AboutPage from "./pages/aboutpage";
import AboutSafetyPage from "./pages/aboutsafetypage";
import CustomizeSpiralPage from "./pages/customizespiralpage";
import CustomizeSubliminalPage from "./pages/customizesubliminalpage";
import CustomizeOverlayPage from "./pages/customizeoverlaypage";
import CustomizeSubliminalMessagesPage from "./pages/customizesubliminalmessagespage";
import CustomizeSubliminalTimingPage from "./pages/customizesubliminaltimingpage";
import CustomizeSpiralTimingPage from "./pages/customizespiraltimingpage";
import CustomizeSubliminalFontPage from "./pages/customizesubliminalfontpage";


const App = () => {
    const [active, setActive] = useState(false);
    const [bgColor, setBgColor] = useState({r: 0, g: 0, b: 0} as RgbColor);
    const [spColor, setSpColor] = useState({r: 96, g: 255, b: 43} as RgbColor);
    const [txtColor, setTxtColor] = useState({r: 255, g: 255, b: 255} as RgbColor);
    const [spinSpeed, setSpinSpeed] = useState(1);
    const [throbSpeed, setThrobSpeed] = useState(1);
    const [throbStrength, setThrobStrength] = useState(1);
    const [zoom, setZoom] = useState(1);
    const [messages, setMessages] = useState([] as string[]);
    const [messageAlpha, setMessageAlpha] = useState(0.25);
    const [messageDuration, setMessageDuration] = useState(0.1);
    const [messageGap, setMessageGap] = useState(1);
    const [oneWord, setOneWord] = useState(true);
    const [randomOrder, setRandomOrder] = useState(true);
    const [bgImage, setBgImage] = useState("");
    const [bgImageAlpha, setBgImageAlpha] = useState(0.5);
    const [trapCursor, setTrapCursor] = useState(false);
    const [textWall, setTextWall] = useState(false);
    const [customGoogleFont, setCustomGoogleFont] = useState("");

    if (active) {
        return (
            <h1>Active</h1>
        )
    } else {
        return (
            <div>
                <React.StrictMode>
                    <div className="horizontal">
                        <RgbColorPicker color={bgColor} onChange={setBgColor}/>
                        <RgbColorPicker color={spColor} onChange={setSpColor}/>
                        <RgbColorPicker color={txtColor} onChange={setTxtColor}/>
                    </div>
                </React.StrictMode>
            </div>
        );
    }
};

const HistoryManager = (() => {
    let historyId = 0;
    return () => {
        let location = useLocation();
        let navigate = useNavigate();

        // useEffect(() => {
        //     window.addEventListener("popstate", (event: PopStateEvent) => {
        //         if (event.state < historyId) navigate(-1);
        //     });
        // }, []);
        //
        // useEffect(() => {
        //     window.history.pushState(historyId, "");
        //     historyId++;
        // }, [location]);

        return <Outlet/>;
    }
})();

const routes = [
    {
        element: <HistoryManager/>,
        children: [
            {
                path: "/",
                element: <Homepage/>
            },
            {
                path: "/spiral",
                element: <SpiralPage/>
            },
            {
                path: "/first-time-safety",
                element: <FirstTimeSafetyPage/>
            },
            {
                path: "/customize",
                element: <CustomizePage/>
            },
            {
                path: "/customize/spiral",
                element: <CustomizeSpiralPage/>
            },
            {
                path: "/customize/spiral/timing",
                element: <CustomizeSpiralTimingPage/>
            },
            {
                path: "/customize/subliminal",
                element: <CustomizeSubliminalPage/>
            },
            {
                path: "/customize/subliminal/messages",
                element: <CustomizeSubliminalMessagesPage/>
            },
            {
                path: "/customize/subliminal/timing",
                element: <CustomizeSubliminalTimingPage/>
            },
            {
                path: "/customize/subliminal/font",
                element: <CustomizeSubliminalFontPage/>
            },
            {
                path: "/customize/overlay",
                element: <CustomizeOverlayPage/>
            },
            {
                path: "/share"
            },
            {
                path: "/about",
                element: <AboutPage/>
            },
            {
                path: "/about/safety",
                element: <AboutSafetyPage/>
            }
        ]
    }
];

const router = createMemoryRouter(routes, {
    initialEntries: ["/"],
    initialIndex: 1
});



const appElem = document.querySelector('#app');
if (navigator.userAgent.indexOf('AppleWebKit') != -1) {
    appElem.classList.add("is_webkit");
}
const root = createRoot(appElem);
root.render(<RouterProvider router={router}/>);


